body{background:url(../images/Background.jpg) no-repeat center center fixed;background-size:cover;display:flex;flex-direction:column;min-height:100vh}.footer{flex-shrink:0;max-width:1920px;margin:0 auto;border-top:.5px solid #3b7dd6;padding:40px 0 40px;background-color:#fff}.footer__container{display:flex;justify-content:center;align-items:center}.footer__address–°ontainer{display:flex;align-items:center;height:65px;border-right:1px solid #3b7dd6}.footer__logoLink{display:block;width:60px;height:60px;margin-right:30px;text-decoration:none;background:url(../../images/logo-min.svg) center no-repeat;transition:background-image .3s ease}.footer__logoLink:hover{background-image:url(../../images/logo-state-hover-min.svg)}.footer__logoLink:active{background-image:url(../../images/logo-state-pressed-min.svg)}.footer__contacts{display:flex;flex-direction:column;align-items:flex-start;padding-right:180px}.footer__contacts_container{display:flex;align-items:center}.footer__contacts_container:not(:last-child){margin-bottom:5px}.footer__contacts_image{width:24px;height:24px;margin-right:15px}.footer__contacts_link{font-family:"Geologica",Arial,Helvetica,sans-serif;font-style:normal;font-weight:300;font-size:18px;line-height:22.5px;color:#3b7dd6;text-decoration:none;transition:color .3s ease}.footer__contacts_link:hover{color:#2f5e9d}.footer__contacts_link:active{color:#183357}.footer__link{display:flex;align-items:center;height:65px;padding-left:180px}.footer__link_navigate{font-family:"Geologica",Arial,Helvetica,sans-serif;font-style:normal;font-weight:300;font-size:18px;line-height:22.5px;color:#3b7dd6;text-decoration:none;transition:color .3s ease}.footer__link_navigate:hover{color:#2f5e9d}.footer__link_navigate:active{color:#183357}.footer__link_navigate:not(:last-child){margin-right:100px}